import React, { useState, createRef } from 'react';
import { NavLink } from 'react-router-dom';
import store from 'store';
import { useTranslation } from 'react-i18next';
import useOutsideClick from '../../../tools/useOutsideClick';

import './header.scss';
import Basket from '../../Basket';

// big icons
import logo from '../../../assets/components/Wrapper/Header/logo.webp';
import favourite from '../../../assets/components/Wrapper/Header/favourite.svg';
import personal from '../../../assets/components/Wrapper/Header/personal.svg';
import select from '../../../assets/components/Wrapper/Header/select.png';

// small icons
import geoSmall from '../../../assets/components/Wrapper/Header/geo-small.svg';
import phoneSmall from '../../../assets/components/Wrapper/Header/phone-small.svg';
import moreSmall from '../../../assets/components/Wrapper/Header/more-small.svg';
import basket from '../../../assets/components/Wrapper/Header/basket.svg';

// phone operators
import vodafon from '../../../assets/components/Wrapper/Header/phone/mts.png';
import kyivstar from '../../../assets/components/Wrapper/Header/phone/ks.png';

//bread
import home from '../../../assets/bread/home.svg';
import { smallRight } from 'react-icons-kit/entypo/smallRight';
import { Icon } from 'react-icons-kit';

const Header = (props) => {
  const { t, i18n } = useTranslation();
  // state for language
  const [language, setLanguage] = useState(localStorage.getItem('i18nextLng'));
  const [openedModalForLanguage, setOpenedModalForLanguage] = useState(false);

  // state for city
  const [openModalForCity, setOpenedModalForCity] = useState(false);

  // refs stuff
  const langRef = createRef();
  const cityRef = createRef();
  useOutsideClick(cityRef, openModalForCity, () => setOpenedModalForCity(false));
  useOutsideClick(langRef, openedModalForLanguage, () => setOpenedModalForLanguage(false));

  const langClick = (lang) => {
    setLanguage(lang);
    i18n.changeLanguage(lang);
    props.setLang(lang);
  };

  // handlers for city functions
  const setDniproCity = () => {
    props.onCityChange(1);
    setOpenedModalForCity(false);
    const storage = store.get('24rolls');
    storage.customOptions.city = 'Dnipro';
    store.set('24rolls', storage);
    props.onSetBaseName('/dp');
    props.history.push('/dp');
  };
  const setZaporijjaCity = () => {
    props.onCityChange(2);
    setOpenedModalForCity(false);
    const storage = store.get('24rolls');
    storage.customOptions.city = 'Zaporijja';
    store.set('24rolls', storage);
    props.onSetBaseName('/zp');
    props.history.push('/zp');
  };

  const setKharkovCity = () => {
    props.onCityChange(3);
    setOpenedModalForCity(false);
    const storage = store.get('24rolls');
    storage.customOptions.city = 'Kharkov';
    store.set('24rolls', storage);
    props.onSetBaseName('/kh');
    props.history.push('/kh');
  };

  let modalForCityStyles = 'modal-window-for-city modal-window-closed';
  if (openModalForCity) {
    modalForCityStyles = 'modal-window-for-city modal-window-opened';
  }
  let modalForLangStyles = 'modal-window-for-language modal-window-closed';
  if (openedModalForLanguage) {
    modalForLangStyles = 'modal-window-for-language modal-window-opened';
  }

  const path = props.match.path.slice(0, 3);
  const { lang } = props;

  const historyArr = props.location.pathname
    .slice(4)
    .split('/')
    .filter((el) => el !== '');
  if (historyArr[0] === 'shares') {
    historyArr[0] = {
      label: lang === 'ua' ? '' : '',
      to: '/shares',
    };
  } else if (historyArr[0] === 'delivery') {
    historyArr[0] = {
      label: '',
      to: '/delivery',
    };
  } else if (historyArr[0] === 'about-us') {
    historyArr[0] = {
      label: lang === 'ua' ? '' : '',
      to: '/about-us',
    };
  } else if (historyArr[0] === 'posts') {
    historyArr[0] = {
      label: '',
      to: '/posts',
    };
    if (props.breadPosts && props.breadPosts[0] && historyArr[1]) {
      historyArr[1] = {
        label: props.breadPosts.filter((el) => el.route === historyArr[1])[0].route,
        to: `${historyArr[0].to}/` + props.breadPosts.filter((el) => el.route === historyArr[1])[0].route,
      };
    }
  } else if (props.categories && props.categories.filter((el) => el.route === historyArr[0])[0]) {
    historyArr[0] = {
      label:
        lang === 'ua'
          ? props.categories.filter((el) => el.route === historyArr[0])[0].name_ua
          : props.categories.filter((el) => el.route === historyArr[0])[0].name_ru,
      to: `/${props.categories.filter((el) => el.route === historyArr[0])[0].route}`,
    };
    if (props.breadProd && props.breadProd[0] && historyArr[1]) {
      historyArr[1] = {
        label:
          lang === 'ua' && props.breadProd.filter((el) => el.route === historyArr[1])[0]
            ? props.breadProd.filter((el) => el.route === historyArr[1])[0].name_ua
            : props.breadProd.filter((el) => el.route === historyArr[1])[0].name_ru,
        to: `${historyArr[0].to}/${props.breadProd.filter((el) => el.route === historyArr[1])[0].route}`,
      };
    }
  } else if (props.filters && props.filters.filter((el) => el.route === historyArr[0])[0]) {
    historyArr[0] = {
      label:
        lang === 'ua'
          ? props.filters.filter((el) => el.route === historyArr[0])[0].name_ua
          : props.filters.filter((el) => el.route === historyArr[0])[0].name_ru,
      to: `/${props.filters.filter((el) => el.route === historyArr[0])[0].route}`,
    };
    if (historyArr[1]) {
      historyArr[1] = {
        label:
          lang === 'ua'
            ? props.breadProd.filter((el) => el.route === historyArr[1])[0].name_ua
            : props.breadProd.filter((el) => el.route === historyArr[1])[0].name_ru,
        to: `${historyArr[0].to}/${props.breadProd.filter((el) => el.route === historyArr[1])[0].route}`,
      };
    }
  }

  return (
    <header className='header'>
      <div className='header__column column'>
        <div className='left-menu'>
          <div className='column'>
            {/* for big screens */}
            <div
              className='left-menu__language'
              onClick={() => {
                setOpenedModalForLanguage(!openedModalForLanguage);
                setOpenedModalForCity(false);
              }}
            >
              <span>{language}</span>
              <img alt='select' src={select} />
            </div>
            {/* modal for language */}
            <div ref={langRef} className={modalForLangStyles}>
              <ul>
                <li
                  key={'li-1'}
                  onClick={() => {
                    langClick('ru');
                    setOpenedModalForLanguage(false);
                  }}
                >
                  RU
                </li>
                <hr style={{ margin: '0' }} />
                <li
                  key={'li-2'}
                  onClick={() => {
                    langClick('ua');
                    setOpenedModalForLanguage(false);
                  }}
                >
                  UA
                </li>
              </ul>
            </div>

            <div
              onClick={() => {
                setOpenedModalForCity(!openModalForCity);
                setOpenedModalForLanguage(false);
              }}
              className='left-menu__city'
            >
              <span>{t(`components.header.city.${props.city}`)}</span>
              <img alt='select' src={select} style={{ marginTop: '5px', marginLeft: '5px' }} />
            </div>
            {/* modal for city */}
            <div ref={cityRef} className={modalForCityStyles}>
              <ul>
                <li key={'li-3'} onClick={setDniproCity}>
                  {t('components.header.city.1')}
                </li>
                <hr style={{ margin: '0' }} />
                <li key={'li-4'} onClick={setZaporijjaCity}>
                  {t('components.header.city.2')}
                </li>
                <hr style={{ margin: '0' }} />
                <li key={'li-5'} onClick={setKharkovCity}>
                  {t('components.header.city.3')}
                </li>
              </ul>
            </div>
            <div className='left-menu__phone'>
              <div className='left-menu__phone__spans'>
                <span className='left-menu__phone__spans__span1'>
                  <img alt='vodafon' src={vodafon} width={30} height={30} />
                  (050)719-24-24
                </span>
                <span className='left-menu__phone__spans__span2'>
                  <img alt='kyivstar' src={kyivstar} width={30} height={30} />
                  (098)560-44-24
                </span>
              </div>
            </div>

            {/* for small screens */}
            <div className='left-menu_for-small-screens'>
              <div className='left-menu__item'>
                <NavLink to={props.match.path.slice(0, 3) + '/change-city'}>
                  <img src={geoSmall} alt='Geo' />
                </NavLink>
              </div>
              <div
                className='left-menu__item'
                style={{
                  borderLeft: '2px solid white',
                  borderRight: '2px solid white',
                }}
              >
                <NavLink to={props.match.path.slice(0, 3) + '/contacts'}>
                  <img src={phoneSmall} alt='phone' />
                </NavLink>
              </div>
              <div className='left-menu__item'>
                <NavLink to={path + '/favourite'}>
                  <img src={favourite} alt='img'></img>
                </NavLink>
              </div>
            </div>
          </div>
        </div>

        <div className='pure' />

        <div className='right-menu'>
          <div className='right-menu__items'>
            {/* for big screens */}
            <div className='right-menu__items__item bordered'>
              <Basket {...props} src={basket} style={'open-modalThree'} presents={props.presents} />
            </div>
            <div className='right-menu__items__item bordered'>
              <NavLink to={path + '/favourite'}>
                <img src={favourite} alt='img' />
              </NavLink>
            </div>
            <div className='right-menu__items__item'>
              <NavLink to={path + (props.isAuthorized ? '/order/personal' : '/log-in')}>
                <img src={personal} alt='img' />
              </NavLink>
            </div>

            {/* for small screens */}
            <div className='right-menu_for-small-screens'>
              <div className='left-menu__item'>
                <Basket {...props} src={basket} style={'open-modalThree'} presents={props.presents} smallMode />
              </div>
              <div
                className='left-menu__item'
                style={{
                  borderLeft: '2px solid white',
                  borderRight: '2px solid white',
                }}
              >
                <NavLink to={path + (props.isAuthorized ? '/order/personal' : '/log-in')}>
                  <img src={personal} alt='personal' />
                </NavLink>
              </div>
              <div className='left-menu__item'>
                <NavLink to={path + '/main-mobile/menu'}>
                  <img src={moreSmall} alt='more' />
                </NavLink>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div className='bottom__header'>
        <div className='bottom__header__nav__logo'>
          {/* for big screens */}
          <img
            style={{ cursor: 'pointer' }}
            onClick={() => props.history.push(props.match.path.slice(0, 3) + '/')}
            src={logo}
            alt='24rolls'
            className='bottom__header_logo'
          />
          {/* for small screens */}
          <img
            onClick={() => props.history.push(props.match.path.slice(0, 3) + '/')}
            style={{ cursor: 'pointer' }}
            src={logo}
            width={135}
            height={62}
            alt='24rolls'
            className='bottom__header_logo-small'
          />
        </div>

        {/* for huge screens */}
        <div className='bottom__header__items'>
          <div className='bottom__header__left-list'>
            <ul>
              <li key={'h-li-1'} className='bordered_grey'>
                <NavLink to={path + '/menu'} exact activeClassName='green-item'>
                  {t('components.header.menu.1')}
                </NavLink>
              </li>
              <li key={'h-li-2'} className='bordered_grey'>
                <NavLink to={path + '/shares'} activeClassName='green-item'>
                  {t('components.header.menu.2')}
                </NavLink>
              </li>
              <li key={'h-li-3'}>
                <NavLink to={path + '/delivery'} activeClassName='green-item'>
                  {t('components.header.menu.3')}
                </NavLink>
              </li>
            </ul>
          </div>
          <div className='pure' />
          <div className='bottom__header__right-list'>
            <ul>
              <li key={'h-li-4'} className='bordered_grey' style={{ borderLeft: '2px solid #C4C4C4' }}>
                <NavLink to={path + '/about-us'} activeClassName='green-item'>
                  {t('components.header.menu.4')}
                </NavLink>
              </li>
              <li key={'h-li-4'} className='bordered_grey'>
                <NavLink to={path + '/reviews'} activeClassName='green-item'>
                  {t('components.header.menu.6')}
                </NavLink>
              </li>
              <li key={'h-li-5'}>
                <NavLink to={path + '/posts'} activeClassName='green-item'>
                  {t('components.header.menu.5')}
                </NavLink>
              </li>
            </ul>
          </div>
        </div>
      </div>
      <div className='bread'>
        <img
          className='bread_home'
          onClick={() => props.history.push(props.match.path.slice(0, 3) + '/')}
          src={home}
          alt=''
        />
        <Icon icon={smallRight} />
        <p onClick={() => props.history.push(props.match.path.slice(0, 3) + '/')}>
          {lang === 'ua' ? '' : ''}
        </p>
        {historyArr.map((el, index) => (
          <>
            {el.to ? (
              <>
                <Icon icon={smallRight} />
                <p onClick={() => props.history.push(props.match.path.slice(0, 3) + el.to)}>{el.label}</p>
              </>
            ) : null}
          </>
        ))}
      </div>
    </header>
  );
};

export default Header;
