import React from "react";
import { backendUrl } from "../../config/config";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import { useTranslation } from "react-i18next";
import LazyLoad from "react-lazy-load";
import Truncate from "react-truncate";

import "./index.scss";
import { useCustomStore } from "../../context/useStore";
import { ACTIONS } from "../../context/providerContext";

const SetsSlider = (props) => {
  const { dispatch } = useCustomStore();
  const { t } = useTranslation();
  const settings = {
    dots: false,
    infinite: true,
    speed: 500,
    slidesToShow: 4,
    slidesToScroll: 1,
    initialSlide: 0,
    responsive: [
      {
        breakpoint: 1550,
        settings: {
          slidesToShow: props.list.length >= 4 ? 4 : props.list.length,
        },
      },
      {
        breakpoint: 1150,
        settings: {
          slidesToShow: props.list.length >= 3 ? 3 : props.list.length,
        },
      },
      {
        breakpoint: 1060,
        settings: {
          slidesToShow: props.list.length >= 4 ? 4 : props.list.length,
        },
      },
      {
        breakpoint: 992,
        settings: {
          slidesToShow: props.list.length >= 4 ? 4 : props.list.length,
        },
      },
      {
        breakpoint: 768,
        settings: {
          slidesToShow: props.list.length >= 3 ? 3 : props.list.length,
        },
      },
    ],
  };

  console.log(props.list);

  return (
    <div className={"sets_slider"}>
      {props.title && (
        <div className={"header-block"}>
          <div className={"header-block_title"}>
            <h3>{t("components.setsSlider.title")}</h3>
          </div>
        </div>
      )}
      <Slider {...settings} className={"slider-block"}>
        {props.list.map((el) => (
          <div key={el._id} className={"list"}>
            <div
              className={"image"}
              style={{
                cursor: "pointer",
              }}
              onClick={() =>
                props.history.push(
                  props.match.path.slice(0, 3) +
                    `/${el.categoryId.route}/${el.route}`
                )
              }
            >
              <LazyLoad
                debounce={false}
                offsetVertical={500}
                offsetHorizontal={1000}
              >
                <img src={backendUrl + "/" + el.image} alt={el.alt} />
              </LazyLoad>
            </div>
            <p>{el.name_ru}</p>
            <div className="item-block">
              <div
                style={{
                  overflow: "hidden",
                  height: "60px",
                }}
              >
                <span className="descr">
                  {el.description_ru.length > 60
                    ? el.description_ru.slice(0, 60) + "..."
                    : el.description_ru}
                </span>
              </div>
              <div className="footer">
                <div>
                  <h2>
                    {((el.price * (100 - el.percent)) / 100).toFixed(0)} .
                  </h2>
                </div>
                <span>
                  <p>{el.weight} .</p>
                </span>
              </div>
            </div>
            <span
              className="plus"
              onClick={
                props.links
                  ? () => dispatch({ type: ACTIONS.ADD_TO_BASKET, payload: el })
                  : () =>
                      dispatch({
                        type: ACTIONS.ADD_TO_BASKET_YOUR_SET,
                        payload: el,
                      })
              }
            >
              +
            </span>
          </div>
        ))}
      </Slider>
    </div>
  );
};

export default SetsSlider;
