import React from "react";
import { Link } from "react-router-dom";

import CustomFoodBlock from "../../../../components/CustomFoodBlock";

import "./index.scss";

const MainSwiper = (props) => {
  return (
    <div className={"main-swiper"}>
      {props.slider && (
        <>
          {props.slider.categoryId && props.slider.categoryId.name_ru && (
            <div className={"main-swiper_header"}>
              <h2>{props.slider.categoryId.name_ru}</h2>
            </div>
          )}
          <div className={"wrapper"}>
            {props.slider.items.map((el) => (
              <CustomFoodBlock
                {...props}
                key={el._id}
                food={el}
                style={false}
                margins={true}
              />
            ))}
          </div>
          {props.more && (
            <div className={"main-swiper_footer"}>
              <button>
                <Link
                  to={
                    props.slider.categoryId
                      ? props.match.path.slice(0, 3) +
                        "/" +
                        props.slider.categoryId.route
                      : props.match.path.slice(0, 3) + "/"
                  }
                >
                  {props.more}
                </Link>
              </button>
            </div>
          )}
        </>
      )}
    </div>
  );
};

export default MainSwiper;
