import React, { useState } from "react";
import { NavLink } from "react-router-dom";
import { backendUrl } from "../../../config/config";
import { ACTIONS } from "../../../context/providerContext";
import { useCustomStore } from "../../../context/useStore";

import { CgMoreO } from "react-icons/cg";
import { IoMdArrowDropdownCircle } from "react-icons/io";

import "./mobileCategories.scss";

const MobileCategories = (props) => {
  const [onShowSecondRow, setOnShowSecondRow] = useState(false);
  const { dispatch } = useCustomStore();

  let firstRow = null;
  let secondRow = null;
  if (props.categories) {
    const categories = props.categories.filter((el) => {
      return el._id !== "5ff17a7f6158c22e503feaa1";
    });
    console.log(firstRow);
    firstRow = categories.slice(0, 6);
    secondRow = categories.slice(6, 12);
  }

  console.log(firstRow);

  return (
    <div className="mobile-categories">
      <div
        className={
          onShowSecondRow
            ? "mobile-categories-container mobile-categories-container-first"
            : "mobile-categories-container"
        }
      >
        {firstRow &&
          firstRow.map((el) => (
            <>
              <NavLink
                onClick={() => dispatch({ type: ACTIONS.DROP_FILTER })}
                key={el._id}
                to={props.match.path.slice(0, 3) + "/" + el.route}
              >
                <div style={{ position: "relative" }}>
                  <img
                    className="mobile-categories-container__item"
                    src={backendUrl + "/" + el.image}
                    alt={el.alt}
                  />
                </div>
              </NavLink>
            </>
          ))}
      </div>
      <div
        className={
          onShowSecondRow
            ? "switcher_container switcher_container_first"
            : "switcher_container"
        }
      >
        <div className="mobile-categories-container_switcher">
          {onShowSecondRow ? (
            <IoMdArrowDropdownCircle
              onClick={() => setOnShowSecondRow((val) => !val)}
              className="icon_more"
            />
          ) : (
            <CgMoreO
              onClick={() => setOnShowSecondRow((val) => !val)}
              className="icon_more"
            />
          )}
        </div>
      </div>
      <div
        className={
          onShowSecondRow
            ? "mobile-categories-container-second visible"
            : "mobile-categories-container-second not-visible"
        }
      >
        {secondRow &&
          secondRow.map((el) => (
            <NavLink
              onClick={() => dispatch({ type: ACTIONS.DROP_FILTER })}
              key={el._id}
              to={props.match.path.slice(0, 3) + "/" + el.route}
            >
              <img
                className="mobile-categories-container__item"
                src={backendUrl + "/" + el.image}
                alt={el.alt}
              />
            </NavLink>
          ))}
      </div>
    </div>
  );
};

export default MobileCategories;
